'use client'

import { useState } from 'react'

export default function ContactPage() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    company: '',
    message: ''
  })
  const [errors, setErrors] = useState<{[key: string]: string}>({})
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitStatus, setSubmitStatus] = useState<'idle' | 'success' | 'error'>('idle')

  const validateForm = () => {
    const newErrors: {[key: string]: string} = {}

    if (!formData.name.trim()) {
      newErrors.name = 'Name is required'
    }

    if (!formData.email.trim()) {
      newErrors.email = 'Email is required'
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = 'Please enter a valid email address'
    }

    if (!formData.message.trim()) {
      newErrors.message = 'Message is required'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }))
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) {
      return
    }

    setIsSubmitting(true)
    setSubmitStatus('idle')

    try {
      const response = await fetch('/api/contact', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      })

      if (response.ok) {
        setSubmitStatus('success')
        setFormData({ name: '', email: '', company: '', message: '' })
      } else {
        setSubmitStatus('error')
      }
    } catch (error) {
      console.error('Error submitting form:', error)
      setSubmitStatus('error')
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <div className="min-h-screen py-16">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Page Header */}
        <div className="text-center mb-16">
          <h1 className="text-4xl md:text-5xl font-bold text-black mb-6">Contact TRANSKO</h1>
          <p className="text-xl text-gray-700 max-w-3xl mx-auto">
            Ready to discuss your environmental management needs? Get in touch with Algeria's leading waste treatment specialists.
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-16">
          {/* Contact Information */}
          <div>
            <h2 className="text-3xl font-bold text-black mb-8">Get in Touch</h2>
            
            {/* Company Info */}
            <div className="mb-8">
              <h3 className="text-xl font-semibold text-black mb-4">TRANSKO Headquarters</h3>
              <div className="space-y-3 text-gray-700">
                <p>Complex: Beside CET Bamandil, Ouargla</p>
                <p>Adm: Industrial Zone Irara 2, HMD Ouargla</p>
                <p>Phone: +213 661 353 443</p>
                <p>Phone: +213 664 024 943</p>
                <p>Phone: +213 020 857 304</p>
                <p>Email: <a href="mailto:Contact@transko.dz" className="text-black underline">Contact@transko.dz</a></p>
                <p>Website: <a href="https://www.transko.dz" target="_blank" rel="noopener noreferrer" className="text-black underline">www.transko.dz</a></p>
                <p className="flex items-start">
                  <span className="font-medium text-black w-20">CEO:</span>
                  <span>SAID .K</span>
                </p>
                <p className="flex items-start">
                  <span className="font-medium text-black w-20">Coverage:</span>
                  <span>Nationwide Algeria</span>
                </p>
              </div>
            </div>

            {/* Services Overview */}
            <div className="mb-8">
              <h3 className="text-xl font-semibold text-black mb-4">Our Services</h3>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <div className="bg-gray-50 p-4">
                  <h4 className="font-medium text-black mb-2">Waste Treatment</h4>
                  <p className="text-sm text-gray-600">Comprehensive hazardous and special waste management</p>
                </div>
                <div className="bg-gray-50 p-4">
                  <h4 className="font-medium text-black mb-2">Site Decontamination</h4>
                  <p className="text-sm text-gray-600">Industrial site cleanup and soil remediation</p>
                </div>
                <div className="bg-gray-50 p-4">
                  <h4 className="font-medium text-black mb-2">Environmental Consulting</h4>
                  <p className="text-sm text-gray-600">Compliance support and environmental audits</p>
                </div>
                <div className="bg-gray-50 p-4">
                  <h4 className="font-medium text-black mb-2">Emergency Response</h4>
                  <p className="text-sm text-gray-600">24/7 environmental emergency services</p>
                </div>
              </div>
            </div>

            {/* Industries Served */}
            <div>
              <h3 className="text-xl font-semibold text-black mb-4">Industries We Serve</h3>
              <div className="flex flex-wrap gap-2">
                {[
                  'Oil & Gas', 'Healthcare', 'Manufacturing', 'Construction', 
                  'Mining', 'Utilities', 'Agriculture', 'Public Sector'
                ].map((industry) => (
                  <span key={industry} className="bg-black text-white px-3 py-1 text-sm">
                    {industry}
                  </span>
                ))}
              </div>
            </div>
          </div>

          {/* Contact Form */}
          <div>
            <div className="bg-gray-50 p-8">
              <h2 className="text-2xl font-bold text-black mb-6">Send Us a Message</h2>
              
              {submitStatus === 'success' && (
                <div className="bg-green-50 border border-green-200 text-green-800 px-4 py-3 mb-6">
                  <p className="font-medium">Thank you for your message!</p>
                  <p className="text-sm">We'll get back to you within 24 hours.</p>
                </div>
              )}

              {submitStatus === 'error' && (
                <div className="bg-red-50 border border-red-200 text-red-800 px-4 py-3 mb-6">
                  <p className="font-medium">Sorry, there was an error sending your message.</p>
                  <p className="text-sm">Please try again or contact us directly.</p>
                </div>
              )}

              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Name Field */}
                <div>
                  <label htmlFor="name" className="block text-sm font-medium text-black mb-2">
                    Full Name *
                  </label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 border ${errors.name ? 'border-red-500' : 'border-gray-300'} focus:ring-2 focus:ring-black focus:border-transparent outline-none transition-colors`}
                    placeholder="Enter your full name"
                  />
                  {errors.name && <p className="text-red-500 text-sm mt-1">{errors.name}</p>}
                </div>

                {/* Email Field */}
                <div>
                  <label htmlFor="email" className="block text-sm font-medium text-black mb-2">
                    Email Address *
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 border ${errors.email ? 'border-red-500' : 'border-gray-300'} focus:ring-2 focus:ring-black focus:border-transparent outline-none transition-colors`}
                    placeholder="Enter your email address"
                  />
                  {errors.email && <p className="text-red-500 text-sm mt-1">{errors.email}</p>}
                </div>

                {/* Company Field */}
                <div>
                  <label htmlFor="company" className="block text-sm font-medium text-black mb-2">
                    Company/Organization
                  </label>
                  <input
                    type="text"
                    id="company"
                    name="company"
                    value={formData.company}
                    onChange={handleInputChange}
                    className="w-full px-4 py-3 border border-gray-300 focus:ring-2 focus:ring-black focus:border-transparent outline-none transition-colors"
                    placeholder="Enter your company name (optional)"
                  />
                </div>

                {/* Message Field */}
                <div>
                  <label htmlFor="message" className="block text-sm font-medium text-black mb-2">
                    Message *
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    rows={6}
                    value={formData.message}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 border ${errors.message ? 'border-red-500' : 'border-gray-300'} focus:ring-2 focus:ring-black focus:border-transparent outline-none transition-colors resize-vertical`}
                    placeholder="Tell us about your environmental management needs, project requirements, or any questions you have..."
                  />
                  {errors.message && <p className="text-red-500 text-sm mt-1">{errors.message}</p>}
                </div>

                {/* Submit Button */}
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className={`w-full py-4 px-6 text-lg font-semibold transition-colors ${
                    isSubmitting 
                      ? 'bg-gray-400 text-gray-700 cursor-not-allowed' 
                      : 'bg-black text-white hover:bg-gray-800'
                  }`}
                >
                  {isSubmitting ? 'Sending Message...' : 'Send Message'}
                </button>
              </form>

              <div className="mt-6 text-center text-sm text-gray-600">
                <p>* Required fields</p>
                <p className="mt-2">We typically respond within 24 hours during business days.</p>
              </div>
            </div>
          </div>
        </div>

        {/* Additional Information */}
        <div className="mt-20">
          <div className="bg-black text-white p-8 md:p-12 text-center">
            <h2 className="text-3xl font-bold mb-6">Why Choose TRANSKO?</h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              <div>
                <h3 className="text-xl font-semibold mb-4">Nationwide Coverage</h3>
                <p className="text-gray-300">
                  Strategic location in Ouargla Province with services across all regions of Algeria
                </p>
              </div>
              <div>
                <h3 className="text-xl font-semibold mb-4">Regulatory Compliance</h3>
                <p className="text-gray-300">
                  Accredited by the Ministry of the Environment with ISO certification in progress
                </p>
              </div>
              <div>
                <h3 className="text-xl font-semibold mb-4">Proven Expertise</h3>
                <p className="text-gray-300">
                  Years of experience serving Algeria's energy, industrial, and healthcare sectors
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
